import React, { Component } from 'react'
import { Segment, Modal, Dimmer, Rating, Card, Image, Icon, Container, Pagination } from 'semantic-ui-react'
import axios from 'axios';
import Navbar from './Navbar'
import {loadinganim} from './loading.gif'

class Bookitem extends Component {

	constructor(props) {
		super(props);

		this.state = {

			page: 0,
			data2: [],
			email: '',
			open: false,
			active: true,
			search: false



		};
		this.PageChange = this.PageChange.bind(this)
	}

	componentWillMount() {

		const query = this.props.match.params.query

		if (query) {
			
			this.setState({ active: true, search: true })
			axios.get('/search/' + this.props.match.params.query + '/' + this.state.page).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })
		}
		else {

			this.setState({ active: true })
			axios.get('/books/' + this.state.page).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })

		}


	}

	componentWillReceiveProps(nextProps){
		const query = this.props.match.params.query

		if (query) {
			
			this.setState({ active: true, search: true,page:0 })
			axios.get('/search/' + nextProps.match.params.query + '/' + this.state.page).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })
		}
		else {

			this.setState({ active: true })
			axios.get('/books/' + this.state.page).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })

		}
	}


	PageChange(event, data) {

		if (this.state.search) {
			this.setState({ active: true })
			this.setState({ data2: [], page: (data.activePage - 1) })
			axios.get('/search/' + this.props.match.params.query + '/' + (data.activePage - 1)).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })

		}

		else {
			this.setState({ active: true })
			this.setState({ page: (data.activePage - 1) })
			this.setState({ data2: [] })
			axios.get('/books/' + (data.activePage - 1)).then(res => { this.setState({ data2: res.data }); this.setState({ active: false }) })
		}




	}

	genurl(isbn) {

		let url = 'http://covers.openlibrary.org/b/isbn/' + isbn + '-L.jpg';
		return url;
	}

	genbookurl(isbn, title, author, avgrating, totalratings, reviews) {
		let url = '/reviews/' + isbn + '/' + title + '/' + author + '/' + avgrating + '/' + totalratings + '/' + reviews;
		return url
	}

	render() {
		const { active } = this.state


		return (
			<div>

				<Dimmer active={active} page>
					<Image src={require('../loading.gif')} />
					<p>Fetching data</p>
				</Dimmer>
				<Navbar style={{ marginbottom: '2em' }} />

				<style>
					{`
       body {
        
  
      }
      
    }
    `}
				</style>
				<Segment basic />
				<Container>

					<div style={{ padding: "0em 0em", textAlign: "center" }}>
						<div style={{ textAlign: "center", padding: '4rem' }}>
							<Pagination defaultActivePage={1} activePage={this.state.page + 1} totalPages={10} onPageChange={this.PageChange} /></div>

						<Card.Group stackable={true} doubling={true} >

							{this.state.data2.map(card => (
								<Card href={this.genbookurl(card.isbn, card.title, card.author, (card.average_rating).toFixed(2), card.ratings_count, card.reviews)} style={{ width: '250px' }}>

									<Image src={this.genurl(card.isbn)} size='massive' wrapped style={{ width: '250px' }} />

									<Card.Content>
										<Card.Header>{card.title}</Card.Header>
										<Card.Meta>
											<span className='date'>Author:{card.author}</span>
										</Card.Meta>
										<Card.Content >
											<Rating icon='star' defaultRating={Math.round(card.average_rating, 2)} maxRating={5} disabled />
										</Card.Content>
										<Card.Description>
											{(card.average_rating).toFixed(2)} Avg rating, {card.ratings_count} total ratings.
      									</Card.Description>
									</Card.Content>
									<Card.Content >
										<a>
											<Icon name='pencil alternate' />
											{card.reviews} Reviews
      									</a>
									</Card.Content>
								</Card>
							))}

						</Card.Group>

					</div>
					<div style={{ textAlign: "center", padding: '4rem' }}>
						<Pagination defaultActivePage={1} activePage={this.state.page + 1} totalPages={10} onPageChange={this.PageChange} /></div>

				</Container>
			</div>
		)
	}
}

export default Bookitem